import React from 'react';
// eslint-disable-next-line
import styled from 'styled-components';
import { Github } from 'styled-icons/boxicons-logos/Github';
import { MailSend } from 'styled-icons/boxicons-regular/MailSend';
import { Linkedin } from 'styled-icons/boxicons-logos/Linkedin';

export default function Socials() {
	const socials = [
		{
			link: 'https://github.com/almirmujic',
			icon: <Github title="GitHub" />
		},
		{
			link: 'https://linkedin.com/in/almirmujic',
			icon: <Linkedin title="LinkedIn" />
		},
		{
			link: 'mailto:contact.almir@gmail.com',
			icon: <MailSend title="Send Email" />
		}
	];
	const printSocials = socials.map(({ link, icon }, id) => (
		<a
			key={id}
			href={link}
			target="_blank"
			rel="noopener noreferrer"
		>
			{icon}
		</a>
	));
	return <React.Fragment>{printSocials}</React.Fragment>;
}
