import React from 'react';
import Projects from './Projects';
import { useSpring, animated } from 'react-spring';
import './Development.css';

export default function Development() {
  const props = useSpring({
    opacity: 1,
    from: { opacity: 0 },
    delay: 1000,
    config: { duration: 1000 }
  });
  const props2 = useSpring({
    opacity: 1,
    from: { opacity: 0 },
    delay: 2000,
    config: { duration: 600 }
  });
  return (
    <animated.div style={props} className="Development">
      <h1>Development</h1>
      <animated.div style={props2} className="development-dev-container">
        <Projects />
      </animated.div>
    </animated.div>
  );
}
