import React from 'react';
import './Navbar.css';

export default function Navbar({ active, setActive }) {
  return (
    <nav
      className={`navbar ${active ? 'fixed' : ''}`}
      onClick={() => setActive(!active)}
    >
      <div className={`menu ${active && 'open'}`}>
        <div className="line one" />
        <div className="line two" />
      </div>
    </nav>
  );
}
